.theme-switch__track {
  height: 20px;
  vertical-align: middle;
  line-height: 20px;
  display: inline-block;
  position: relative;
}

.theme-switch__track .theme-switch__toggle--custom {
  box-sizing: border-box;
  width: 40px;
  height: 20px;
  vertical-align: top;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border: none;
  border-radius: 25% / 50%;
  transition: all .2s;
  display: inline-block;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-switch__track .theme-switch__toggle--custom:before {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .15s;
  display: inline-block;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

.theme-switch__track input[type="checkbox"] {
  width: 0;
  height: 0;
  z-index: -1;
  opacity: 0;
  display: block;
  position: absolute;
}

.theme-switch__track input[type="checkbox"]:checked + .theme-switch__toggle--custom {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.theme-switch__track input[type="checkbox"]:checked + .theme-switch__toggle--custom:before {
  transform: translateX(20px);
}

